import React from 'react';
import './product.css';
import Button from '../Button/Button';

const Product = (props)=>{
    const {name, price, bonus, availability, installmentPrice, img} = props;
    return(
        <div className="product">
            <img className="product-image" src={require("../MainPage/PopularProducts/img/"+img)} alt=""/>
            <p className="product-name">{name}</p>
            <p className="product-availability">{availability}</p>
            <p className="product-price">{price}</p>
            <p className="product-bonus">{bonus}</p>
            <p className="product-installment-price">{installmentPrice}</p>
            <Button text="Добавить в корзину" action=""/>
        </div>
    )
};

export default Product;