import React from 'react';
import shares from './shares.json';
import Share from '../../Share/Share';
import './popular-shares.css';

const PopularShares=()=>{
    const renderItem =(arr)=>{
        return(
            arr.map(item=>{
                return(
                    <Share name={item.name} period={item.period} img={item.img}/>
                )
            })
        )
    };

    const renderShares = renderItem(shares);

    return(
        <section className="popular-shares container">
            <h1 className="popular-shares-headline">Горячие акции</h1>
            {renderShares}
        </section>
    )
};

export default PopularShares;