import React from 'react';
import './header-menu.css';

const HeaderMenu=(props)=>{
  const {items} = props;
  const renderItems = (arr)=>{
      return arr.map(item=>{
          return(
              <li className="header__menu-item-wrapper">
                  <a className="header__menu-item" href={item.link}>{item.name}</a>
              </li>
          )
      });
  };
  const renderMenu = renderItems(items);
  return(
      <ul className="header-menu">
          {renderMenu}
      </ul>
  )
};

export default HeaderMenu;