import React from 'react';
import './about-company.css';
import picture from './img/muztorg-background.jpg';
import picture2 from './img/muztorg-background2.jpg';

const AboutCompanyBlock = () =>{
    return(
        <section className="about-company container">
            <h1 className="about-company-headline">О компании</h1>
            <article className="about-company__content">
                <div className="about-company__text-content-half">
                    <p className="about-company__text">
                        Музторг – лидер среди розничных сетей по продаже музыкальных инструментов в России.
                    </p><br/>
                    <p className="about-company__text">Мы – крупнейшая розничная сеть из 45 музыкальных магазинов в России, www.muztorg.ru - интернет-магазин №1 по популярности в данном сегменте  в Рунете*.</p><br/>
                    <p className="about-company__text">У нас представлен широкий ассортимент товаров для начинающих и профессиональных  музыкантов, звукорежиссеров и продюсеров, владельцев бизнесов. Компания - официальный дилер 300 мировых брендов Yamaha, Fender, Ibanez, Gibson, Marshall, Casio, AKG, Shure, Korg, Numark, Pioneer, Tamа, Zildjian, Mackie, JBL и других.</p><br/>
                </div>
                <img className="about-company__image" src={picture} alt=""/>
                <p className="about-company__text">Музторг работает для своих покупателей более 20 лет. Компания первой на российском рынке музыкальных инструментов реализовала омниканальный подход: в магазинах и онлайн для покупателей всегда единые цены, ассортимент и сервис.</p>
            </article>
            <article className="about-company__content">
                <img className="about-company__image" src={picture2} alt=""/>
                <div className="about-company__text-content-half">
                    <ul>
                        <p className="about-company__text">Мы создаем удобные сервисы для покупателей:</p>
                        <li className="about-company__text-list">рассрочка  «0% переплата» и программы кредитования до 36 месяцев  от ведущих банков на лучших условиях,</li>
                        <li className="about-company__text-list">быстрая доставка, 15 300 пунктов самовывоза заказов,</li>
                        <li className="about-company__text-list">музыкальные онлайн-курсы, услуги полной настройки инструмента и страхования покупки,</li>
                        <li className="about-company__text-list">гарантия до 36 месяцев на товары, удобный возврат и обмен,</li>
                        <li className="about-company__text-list">возможность купить по лучшей цене на рынке по программе «Увидел дешевле»,</li>
                        <li className="about-company__text-list">возможность приобрести товар с максимальными скидками в разделе Музторг-Дисконт,</li>
                        <li className="about-company__text-list">кэшбэк 5% на покупки, накопительные скидки 5% и 10% участникам Клубной программы Музторга.</li>
                    </ul>
                </div>
            </article>
        </section>
    );
};

export default AboutCompanyBlock;