import React from 'react';
import './popular-products.css';
import Product from '../../Product/Product';
import popularProducts from './products';

const PopularProducts = () =>{
    const renderItems = (arr)=>{
        return(
            arr.map(item=>{
                return(
                    <Product name={item.name} price={item.price} bonus={item.bonus} availability={item.availability} installmentPrice={item.installmentPrice} img={item.img}/>
                )
            })
        );
    };

    const renderPopularProducts = renderItems(popularProducts);
    return(
        <section className="popular-products container">
            <h1 className="popular-products-headline">Популярные продукты</h1>
            {renderPopularProducts}
        </section>
    )
};

export default PopularProducts;