import React from 'react';
import Logo from '../Logo/Logo';
import HeaderMenu from './HeaderMenu/HeaderMenu';
import Button from '../Button/Button';
import './header.css';

const Header = () =>{
    class headerMenuItem{
        constructor(name, link){
            this.name = name;
            this.link = link;
        }
    }
    let headerMenuItems = [new headerMenuItem("Главная", ""), new headerMenuItem("Каталог", ""), new headerMenuItem("Статьи и обзоры", ""), new headerMenuItem("Акции", "")];
  return(
      <header className="header container">
          <Logo/>
          <HeaderMenu items={headerMenuItems}/>
          <Button text="Войти" action=""/>
      </header>
  )
};

export default Header;