import React from 'react';
import './share.css';
import Button from '../Button/Button';

const Share = (props)=>{
    const {name, period, img} = props;
    let margin;
    margin = period==" "?'22px':'5px';
    return(
        <div className="share">
            <img className="share-img" src={require("../MainPage/PopularShares/img/"+img)} alt=""/>
            <p style={{marginBottom: margin}} className="share-name">{name}</p>
            <p className="share-period">{period}</p>
            <Button text="Подробнее" action=""/>
        </div>
    )
};

export default Share;